:root {
  --FONT-FAMILY: "Roboto", sans-serif;
  --RED: #ff001b;
  --WHITE: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--FONT-FAMILY);
  color: var(--WHITE);
  font-size: 12px;
  font-weight: 500;
}

.SectionHeader {
  opacity: .9;
  background: url("header-jpg.ac0092f0.jpg") 10% 34% / 400% no-repeat;
  height: 230px;
}

.Nav {
  justify-content: space-between;
  align-items: center;
  padding: 40px 20px;
  display: flex;
}

.LogoHeader {
  display: flex;
}

.LogoImage {
  justify-content: center;
  display: flex;
}

.LogoText {
  display: none;
}

.UlNav {
  gap: 40px;
  margin-block: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.LiNav {
  text-transform: uppercase;
  text-align: center;
  color: var(--WHITE);
  line-height: 14.06px;
  text-decoration: none;
  position: relative;
}

.LiNav:after {
  content: "";
  background-color: var(--RED);
  width: 0;
  height: 3px;
  transition: width .3s, left .3s;
  position: absolute;
  bottom: -4px;
  right: 0;
}

.LiNav:hover:after {
  width: 100%;
  left: 0;
}

.Current:after {
  content: "";
  background-color: var(--RED);
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.SearchForm {
  color: var(--WHITE);
  align-items: center;
  width: 280px;
  height: 20px;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.SearchInput {
  color: var(--WHITE);
  background-color: #0000;
  border: none;
  outline: none;
  flex-grow: 1;
  height: 100%;
  font-size: 16px;
}

.SearchButton {
  color: var(--WHITE);
  cursor: pointer;
  background-color: #0000;
  border: none;
}

.SearchIcon {
  fill: var(--WHITE);
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.SearchBorder {
  background-color: var(--WHITE);
  width: 100%;
  height: .5px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (width >= 425px) {
  .SectionHeader {
    background-position: 0 29%;
    background-size: 100%;
    height: 219px;
  }

  .Nav {
    padding: 40px 32px;
  }

  .LogoHeader {
    gap: 10px;
  }

  .LogoText {
    color: var(--WHITE);
    font-size: 30px;
    font-weight: 500;
    line-height: 35.16px;
    display: inline;
  }
}

@media screen and (width >= 769px) {
  .SectionHeader {
    background-position: 0 29%;
    background-size: 100%;
    height: 216px;
  }
}

:root {
  --FONT-FAMILY: "Roboto", sans-serif;
  --RED: #ff001b;
  --ORANGE: #ff6b08;
  --WHITE: #fff;
  --BLACK: #000;
}

body {
  font-family: var(--FONT-FAMILY);
  font-size: 12px;
  font-weight: 500;
}

.IndexContainer {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

@media screen and (width >= 768px) {
  .IndexContainer {
    gap: 32px;
  }
}

@media screen and (width >= 1280px) {
  .IndexContainer {
    gap: 15px;
  }
}

.card {
  cursor: pointer;
  width: 280px;
  margin-bottom: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (width >= 768px) {
  .card {
    width: 336px;
  }
}

@media screen and (width >= 1280px) {
  .card {
    width: 395px;
  }
}

.card:hover {
  transform: scale(1.05);
}

.card img {
  border-radius: 5px;
  width: 100%;
  height: 398px;
}

@media screen and (width >= 768px) {
  .card img {
    gap: 32px;
    height: 455px;
  }
}

@media screen and (width >= 1280px) {
  .card img {
    height: 574px;
  }
}

.card h1 {
  text-transform: uppercase;
  color: var(--BLACK);
  line-height: 1.33;
}

@media screen and (width >= 1280px) {
  .card h1 {
    font-size: 20px;
    line-height: 1.2;
  }
}

.card p {
  color: var(--ORANGE);
  line-height: 1.33;
}

@media screen and (width >= 1280px) {
  .card p {
    font-size: 20px;
    line-height: 1.2;
  }
}

.films-list {
  padding-top: 20px;
  padding-bottom: 40px;
}

@media screen and (width >= 768px) {
  .films-list {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.card .details {
  display: flex;
}

.card .details p {
  margin: 0;
}

.card .details p:not(:last-child):after {
  content: "|";
  color: var(--ORANGE);
  margin-left: 5px;
  margin-right: 5px;
}

.footer {
  text-align: center;
  background-color: #f7f7f7;
  min-width: 320px;
  margin: -8px;
}

@media screen and (width <= 767px) {
  .footer {
    padding-top: 29px;
